h2 {
  margin-left:10px;
  color: black;
  font-family: Fantasy;
  font-weight: bold;
  font-family: 'Chelsea Market', cursive;
  -webkit-text-stroke: 1px black;
  color:black;
  text-shadow:
      3px 3px 0 rgb(213, 202, 202),
    -1px -1px 0 white,  
     1px -1px 0 rgb(151, 148, 148),
     -1px 1px 0 white,
      1px 1px 0 white;
}

#social-em {
  padding-left:15px;
  padding-right:15px;
}

#social-em a {
  background-color: rgb(51, 51, 51);
}

#social-em:hover {
  color: #f39c12;
}

#linked:hover  {
  color: #f39c12;
}